.modal {
    position: fixed;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    background: #fff;
    z-index: 10;
    overflow: hidden;
    border: solid thin lightgray;
    -webkit-transition: all .5s;
    -o-transition: all .5s;
    transition: all .5s;
}

.hide-modal-fade {
    width: 0;
    height: 0;
}

.hide-modal-bottom { top: 100%; }


