.list-view {
    position: relative;
    width: 100%;
}

.list-item {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
    border-bottom: solid 1px lightgray;
    padding: 1em;
    cursor: pointer;
}

.list-item-content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 90%;
    color: grey;
}

.list-item-second-content { 
    width: 20%
}

.lv-title{ color: black }

.list-item-action {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    width: 10%;
    height: 100%;
    -ms-flex-align: center;
        align-items: center;
    font-size: 1.3em;
    color: grey;
}


