.float-button {
    position: fixed;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    width: 18%;
    height: 10%;
    bottom: 2em;
    right: 1em;
    border-radius: 50%;
    padding: 1em;
    -webkit-box-shadow: 2px 2px 5px lightgray;
            box-shadow: 2px 2px 5px lightgray;
    background: deepskyblue;
    color: #fff;
    cursor: pointer;
}

.btn {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;
    padding: 1em;
    border: none;
    background: lightgrey;
    font-size: 1em;
    color: grey;
    text-transform: uppercase;
}

.btn-primary { background: #2196F3; color: #ffff }

@media screen and (min-width: 720px) {
    .float-button { width: 5em; }
    .btn { width: 40%; margin-left: 30%; }
}


