* {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }

a { color: initial; text-decoration: none; }

i[class^="icon"]{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: 1.5em;
  cursor: pointer;
}

.no-found {
  position: absolute;
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background: #fff;
  z-index:-1;
}

.mt-1 { margin-top: 1em }

@media screen and (min-width: 720px) {
  body { background: #e2e2e2 } 
  .container {
    width: 70%;
    margin: auto;
    background: #fff;
    margin-top: 1em;
    margin-bottom: 1em;
  }
}

